import { SnippetConfigs } from '@ali/4ever-cangjie';

export default class SnippetManager {
  configs: SnippetConfigs;

  /**
   * 当前 active 的 snippet 类型和 triggerText
   */
  private current: { trigger?: string; text?: string } = {};

  private isShowing = false;

  constructor(cfg: SnippetConfigs = {}) {
    this.configs = cfg;
  }

  setConfigs(cfg: SnippetConfigs) {
    this.configs = cfg;
  }

  setCurrentSnippet(trigger?: string, text?: string) {
    this.current.trigger = trigger;
    this.current.text = text;
  }

  getSnippet() {
    return this.current;
  }

  isShowingPanel() {
    return this.isShowing;
  }

  setIsShowing(value: boolean) {
    this.isShowing = value;
  }
}